import React, { Suspense,Component } from 'react';
import { withTranslation } from "react-i18next";

const Customer = React.lazy(()=>import("../../../Component/imageComponents/customerImg"));
const PartnerImg = React.lazy(()=>import("../../../Component/imageComponents/partnerImg"));
const Trasaction = React.lazy(()=>import("../../../Component/imageComponents/transactionImg"));
const Map = React.lazy(()=>import("../../../Component/imageComponents/map"));

export class numberStatsSection extends Component {

  constructor() {
    super()

    this.state = {
      items: undefined,

    }
  }

  componentDidMount() {
    const url = 'https://api.nivesh.com/API/GetSummaryDetails';
    const postBody = {
      TotalClient: "",
      TotalPartner: "",
      TotalPartnerOverCities: "",
      NoOfTransaction: "",
      TotalTransactionValue: ""

    };
    const requestMetadata = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'token': '636F8F63-06C4-4D95-8562-392B34025FB0'
      },
      body: JSON.stringify(postBody)
    };

    fetch(url, requestMetadata)
      .then(response => response.json())
      .then(data => {
        this.setState({ items: data.SummaryDetailsList });
      }).catch(error => {
        console.log(error);
      });
  }

  formatNumber = (value) => {
    return (value).toLocaleString('en-IN', {
      maximumFractionDigits: 0
    })
  }

  render() {

    const { t } = this.props;
    const { items } = this.state;

    return (
      <>
      <div id="map-section">
          <div className="container container-wrapper">
            <h2 className="text-center text-white mb-4">{t("Home:hOME60")}</h2>
            <div className="row">
              <div className="col-lg-6 col-md-6">
              <Suspense fallback={<div>Loading....</div>}><Map/></Suspense>
              </div>


                <div className="col-lg-6 col-md-6">
                  <div className="map-flex border border-white p-3">
                    <div className="col-lg-4">
                    <Suspense fallback={<div>Loading....</div>}><PartnerImg/></Suspense>
                    </div>
                    <div className="col-lg-8">
                      <h2 className="text-right text-white">{items ? this.formatNumber(items.TotalPartner):"Loading..."}
                      </h2>
                      <h6 className="text-right text-white">
                        {t("Home:hOME61")} <span>{items ? this.formatNumber(items.TotalPartnerOverCities):"Loading..."}</span> {t("Home:hOME61-A")}
                      </h6>
                    </div>

                  </div>
                  <div className="map-flex border border-white p-3">
                    <div className="col-lg-8">
                      <h2 className="text-left text-white">{items ? this.formatNumber(items.TotalClient):"Loading..."}</h2>
                      <h6 className="text-left text-white">{t("Home:hOME62")}</h6>
                    </div>
                    <div className="col-lg-4">
                    <Suspense fallback={<div>Loading....</div>}><Customer/></Suspense>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </div>

        <div className="gardient-red">
          <div className="container container-wrapper">
            <div className="row row justify-content-between">
              <div className="col-lg-3 border border-white p-5 mb-4">
                <h2 className="text-white text-center">{items ? this.formatNumber(items.NoOfTransaction):"Loading..."}</h2>
                <p className="text-center text-white">{t("Home:hOME63")}</p>
              </div>
              <div className="col-lg-3 border border-white p-5 mb-4 text-center">
              <Suspense fallback={<div>Loading....</div>}><Trasaction/></Suspense>
              </div>
              <div className="col-lg-3 border border-white p-5 mb-4">
                <h2 className="text-white text-center">Rs. {items ? this.formatNumber(parseInt(items.TotalTransactionValue)):"Loading..."}</h2>
                <p className="text-center text-white">{t("Home:hOME64")}</p>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default withTranslation()(numberStatsSection);